@import '../../theme.scss';

.second-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  z-index: 99;
  background-color: #fff;
  border-bottom: 1px solid #e9e9e9;
  .logo {
    float: left;
    position: relative;
    margin-left: 15px;
    margin-right: 15px;
    height: 100%;
    width: 150px;
    h1 {
      position: absolute;
      width: 154px;
      height: 100%;
      top: 0;
      a {
        display: inline-block;
        vertical-align: middle;
        width: 154px;
        height: 38px;
        background: url("../../images/logo.png") no-repeat;
        span{
          position: absolute;
          left: -100px;
        }
      }
      &::before {
        content: "";
        display: inline-block;
        height: 100%;
        vertical-align: middle;
      }
    }
  }
  .title {
    float: left;
    height: 100%;
    position: relative;
    margin-left: 49px;
    p {
      margin: 0;
      height: 21px;
      font-size: 16px;
      //   
      color: rgba(0, 0, 0, 1);
      line-height: 21px;
      display: inline-block;
      vertical-align: middle;
      span {
        color: blue;
        border-bottom: 1px solid blue;
        cursor: pointer;
      }
    }
  }
  .title::before {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
  .title::after {
    display: block;
    content: "";
    position: absolute;
    top: 20px;
    left: -34px;
    width: 1px;
    height: 32px;
    background: rgba(233, 233, 233, 1);
  }
  .second-header-right {
    float: right;
    height: 100%;
    .language-inner{
      float: left;
      height: 100%;
      display: flex;
    }
    .buy-btn {
      float: right;
      height: 100%;
      background: $bg-color;
      a {
        display: block;
        padding: 0 20px;
        height: 70px;
        font-size: 14px;
        //    
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 70px;
        i {
          margin-right: 14px;
        }
      }
    }
  }
}
